
:root {
    --main-clr: #5297d2;
    --main-clr-hvr: #407aab;
}
.txt-dots {
    white-space:nowrap;
    overflow:hidden;text-overflow:ellipsis;
}
.profile-id-label {
    width: 230px;
    line-height: 1;
    display: inline-block;
}
section.banner {
    padding: 170px 0 80px;
}

.banner h2 {
    font-size: 1.2rem;
}

.banner h1 {
    margin-bottom: 20px;
    color: #4d4d4d;
    font-weight: 600;
    font-size: 2.8rem;
    line-height: 1;
}

.all-btn {
    background-color: var(--main-clr);
    color: #fff;
    border: 1px solid var(--main-clr);
    border-radius: 4px;
    font-size: 15px;
    padding: 8px 45px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0,0,0,.12);
}

    .all-btn:hover {
        box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0,0,0,.12);
        color: #fff;
    }



.bg-dimmer {
    background-color: rgba(0, 0, 0, 0.9);
  
   
    position: fixed;
   display:none;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
}

.enlarge-currency {
    display: flex;
    align-items: center;
}


.img-large {
    position: relative;
    z-index: 9999;
    display: flex;
    
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    /* padding: 20px 40px; */
    width: 100%;
    
    /* box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0,0,0,.12);
    */
    /* background-color: #1f1f1f; */
}
.img-large > div {
    margin:0 10px;
}
.img-large > div p {
    color: #fff;
    margin: 0;
    text-align: center;
    font-size: 15px;
    opacity: 0.7;
}


.img-right {
    background-color:#000;
    text-align:right;
    position: absolute;
    right: 0;
    top: 50%;
    z-index: 99999;
}
.img-left {
    background-color:#000;
    text-align:right;
    position: absolute;
    left: 0;
   
    top: 50%;
    z-index: 99999;
}
.img-close {
    background-color:#000;
    text-align:right;
    position: absolute;
    right: 0;
    top: 0;
}
    .img-close i, .img-right i, .img-left i {
        color: #fff;
        margin: 5px 10px;
        font-size: 25px;
        cursor: pointer;
       
    }
    .photo-all .col-md-3 {
        position: initial;
        margin-bottom:25px;
    }
.mt-sec-1 {
    margin-top: 50px;
}

.c-thumb {
    cursor:pointer;
    float: left;
    width: 100%;
}
.bd-over-hide {
    overflow:hidden;
}


.img-large img{
    margin-bottom:10px;
}
.issue-date-head {
    font-size: 22px;
    text-align:center;
    margin-bottom: 18px;
}
.photo-all > .container > .row {
    margin-bottom:20px;
}
.bg-sec-separator {
    background: #f6f6f7;
}
.abt-high-para {
    background-color: #000;
    color: #fff;
    font-size: 1.6rem;
}
.abt-high-para p b {
    font-size:1.8rem;
}
.b-cards {
    transition: transform 420ms cubic-bezier(.165,.84,.44,1);
    width: 100%;
    float: left;
    box-shadow: 0 0 2rem rgba(0,0,0,.1);
    padding: 15px 15px 5px;
}
    .b-cards h4 {font-weight:600;color:#000;}
    .b-cards > p {color:#000;}
    .b-cards .arrow {
        width: 25px;
    }
.b-card-arrow-c {margin-top:80px;color:var(--main-clr);font-weight:600;}

.b-cards:hover {
    box-shadow: 0 0 2rem rgba(0,0,0,.2);
    transform: scale(1.05);
}
.form-cover {
    position:relative;
    width:100%;
    float:left;
    margin-bottom:37px;
}
    .form-cover input {
        width: 100%;
        border: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        outline: none;
        font-size: 14px;
    }
    .form-cover label {
        pointer-events:none;
        opacity:0.7;
        font-size: 14px;
        margin: 0;
        position: absolute;
        left: 0;
        bottom: 3px;
        transition: bottom .2s linear, font-size .2s linear;
    }
.form-label, .form-check-label {
    opacity: 0.7;
    font-size: 14px;
}
.label-up {
    bottom: 20px !important;
    font-size: 13px !important;
}
 .validation-msg {
    position: absolute;
    display: none;
    color: #db3236;
    font-size: 13px;
    margin: 0;
}
.note-identifier {

}

.u-btn {
    border: 1px solid var(--main-clr);
    background-color: var(--main-clr);
    border-radius: 3px;
    color: #fff;
    outline:none;
    font-size: 16px;
    padding: 4px 18px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0,0,0,.12);
}
    .u-btn:hover {
        box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0,0,0,.12);
        
    }
.bank-name-head {
    color:var(--main-clr);
    border-bottom:1px solid #000;
    padding-bottom:5px;
}
.c-thumb img {
    margin: 0 auto;
    display: block;
}

#signup-form .modal-header, #login-form .modal-header {
    padding: .7rem 1rem;
    display: block;
    border: none;
}
    #signup-form .modal-header h4, #login-form .modal-header h4 {
        font-size: 19px;
        font-weight: 600;
    }
#signup-form .modal-content, #login-form .modal-content {
    border-radius: 0;
}






@media (min-width: 576px) {
    #signup-form .modal-dialog, #login-form .modal-dialog {
        max-width: 370px;
        margin: 1.75rem auto;
    }

}
.signup-btn-cover button, .signup-btn-cover input[type=submit] {
    width: 100%;
    border-radius: 0;
    height: 43px;
}
.signup-login-p {
    font-size: 14px;
    opacity: 0.6;
}
    .signup-login-p span, .home-login {
        opacity: 1;
        color: var(--main-clr-hvr);
        font-weight: 600;
        cursor: pointer;
    }
.blog h4 {
    font-size:18px;
    font-weight:600;    
}

.all-blogs {
    margin:0;
    padding:0;
}
    .all-blogs li {
        list-style-type: none;
        margin-bottom: 16px;
        border-bottom: 1px solid #000;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
        .all-blogs li:last-child {
            border-bottom:none;
        }
        .all-blogs li h6 {
            font-weight: 600;
            margin-bottom: 2px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    
    .all-blogs li p a {
        font-size:13px;
    }
.all-blog-head {
    font-size:20px;
    font-weight:600;
    margin-bottom:25px;
}

.all-blogs .blog-side-date {
    font-size: 14px;
    margin-bottom:2px;
}
.all-blogs .article-desc {
    overflow: hidden;
    max-height: 41px;
    font-size: 14px;
    margin-bottom: 0px;
}
.blog-heading-sec {
    background-color: #c1e2ff;
}
.about .content h3 {
    font-weight: 700;
    font-size: 26px;
    color: #3b434a;
    text-transform: uppercase;
}

.about .content p {
    margin-bottom: 0;
}

.about .content ul {
    list-style: none;
    padding: 0;
    margin-top: 20px;
}

    .about .content ul li {
        padding-bottom: 10px;
    }

    .about .content ul i {
        font-size: 20px;
        padding-right: 2px;
        color: var(--main-clr);
    }

.about .content .count-box {
    width: 100%;
}

    .about .content .count-box i {
        display: block;
        font-size: 36px;
        color: #ffb727;
        float: left;
    }

    .about .content .count-box span {
        font-size: 20px;
        line-height: 30px;
        display: block;
        font-weight: 700;
        color: #3b434a;
        margin-left: 50px;
    }

    .about .content .count-box p {
        padding: 15px 0 0 0;
        margin: 0 0 0 50px;
        font-family: "Raleway", sans-serif;
        font-size: 14px;
        color: #5d6a75;
    }

    .about .content .count-box a {
        font-weight: 600;
        display: block;
        margin-top: 20px;
        color: #5d6a75;
        font-size: 15px;
        font-family: "Satisfy", serif;
        transition: ease-in-out 0.3s;
    }

        .about .content .count-box a:hover {
            color: #82909c;
        }

.about .image {
    background: url("../img/me.jpg") center center no-repeat;
    background-size: cover;
    min-height: 500px;
}

.about .skills-content {
    margin-top: 30px;
}

    .about .skills-content .progress {
        height: 50px;
        display: block;
        background: none;
    }

        .about .skills-content .progress .skill {
            padding: 0;
            margin: 0 0 6px 0;
            text-transform: uppercase;
            display: block;
            font-weight: 700;
            font-family: "Raleway", sans-serif;
            color: #3b434a;
        }

            .about .skills-content .progress .skill .val {
                float: right;
                font-style: normal;
            }

    .about .skills-content .progress-bar-wrap {
        background: #e6e8eb;
    }

    .about .skills-content .progress-bar {
        width: 1px;
        height: 10px;
        transition: .9s;
        background-color: #ffb727;
    }


.resume .resume-title {
    font-size: 26px;
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #3b434a;
}

.resume .resume-item {
    padding: 0 0 20px 20px;
    margin-top: -2px;
    border-left: 2px solid #82909c;
    position: relative;
}

    .resume .resume-item h4 {
        line-height: 18px;
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        color: var(--main-clr);
        margin-bottom: 10px;
    }

    .resume .resume-item h5 {
        font-size: 16px;
        background: #eef0f2;
        padding: 5px 15px;
        display: inline-block;
        font-weight: 600;
        margin-bottom: 10px;
    }

    .resume .resume-item ul {
        padding-left: 20px;
    }

        .resume .resume-item ul li {
            padding-bottom: 10px;
        }

    .resume .resume-item:last-child {
        padding-bottom: 0;
    }

    .resume .resume-item::before {
        content: "";
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 50px;
        left: -9px;
        top: 0;
        background: #fff;
        border: 2px solid #82909c;
    }